<template lang="html">
  <div class="">
  <el-button style="margin-left:14px;" @click="dialogTableVisible = true">申请支付</el-button>
  <el-button @click="dialogTableVisible = true">申请开票</el-button>
    <el-dialog title="申请开票" :visible.sync="dialogTableVisible">
      <el-table :data="gridData">
        <el-table-column property="age" label="序号"></el-table-column>
        <el-table-column property="tel" label="手机号"></el-table-column>
        <el-table-column property="name" label="司机姓名"></el-table-column>
        <el-table-column property="carage" label="车牌号"></el-table-column>
        <el-table-column property="zshu" label="运单数"></el-table-column>
        <el-table-column property="zmoney" label="合计运费"></el-table-column>
        <el-table-column property="pre" label="受票方"></el-table-column>
      </el-table>

      <el-form ref="form">
        <p style="float:none;">运单总金额：<span style="color:red;line-height:50px;">1234.34元</span></p>
        <el-form-item label="备注信息:">
          <el-input placeholder="对发票有特殊需求请备注详细"></el-input>
        </el-form-item>
        <el-form-item style="text-align: right;">
          <el-button type="success" @click="dialogTableVisible = false">申请开票</el-button>
          <el-button plain="true" type="success" @click="dialogTableVisible = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
      return {
        gridData: [{
          age: 1,
          tel: 13245643456,
          name: '张光芒',
          carage: '鲁E12345',
          zshu: 1,
          zmoney: 1002345.34,
          pre: '四川通盛钻探有限公司'
        }],
        dialogTableVisible: false,
        dialogFormVisible: false,
      }
    },
    mounted(){
      console.log(this.gridData[1].date)
    }
}
</script>

<style lang="css" scope>
  .el-table{
    font-size: 13px;
    width:700px;
  }
</style>



