<template lang="html">
  <div class="waybill">
    <div class="headers-wrapper" style="border-bottom: 1px solid #e6e9f2;">
      <headers></headers>
    </div>
      <div class="content clearfix">
          <p class="font">运单管理 <span>/ 运单管理说明</span></p>
          <div>
              <el-button>新建运单</el-button>
              <el-button>EXCEL导出</el-button>
          </div>
      </div>
      <div class="form">
            <div class="input">
              <el-form :inline="true" class="demo-form-inline">
                <el-form-item label="运单号">
                  <el-input placeholder="请输入订单号"></el-input>
                </el-form-item>
                <el-form-item label="托运日期">
                  <el-date-picker type="week" format="Week WW" placeholder="选择下单区间"></el-date-picker>
                </el-form-item>
                <el-form-item label="到站城市">
                  <el-input placeholder="选择"></el-input>
                </el-form-item>
                <el-form-item label="收获单位">
                  <el-input placeholder="选择区间"></el-input>
                </el-form-item>
                <el-form-item label="托运单位">
                  <el-input placeholder="选择"></el-input>
                </el-form-item>
                <el-form-item label="司机姓名">
                    <el-input placeholder="选择"></el-input>
                </el-form-item>
                <el-form-item label="订单状态">
                  <el-input placeholder="全部"></el-input>
                </el-form-item>
                <el-form-item label="司机手机号">
                  <el-input placeholder="请输入手机号"></el-input>
                </el-form-item>
                <el-form-item label="车牌号">
                  <el-input placeholder="请输入手机号"></el-input>
                </el-form-item>
                <el-form-item label="本页显示笔数">
                    <el-select placeholder="30笔"></el-select>
                </el-form-item>
                <el-button style="width:190px;background:#3052ac;color:white;border:none;">查询</el-button>
              </el-form>
            </div>

            <div class="table">

              <el-table ref="multipleTable" :data="tableData" border style="width: 100%">
                <el-table-column type="selection">
                </el-table-column>
                <el-table-column label="托运日期" width="120">
                <template scope="scope">{{ scope.row.date }}</template>
                </el-table-column>
                <el-table-column label="货单号" width="120">
                <template scope="scope">{{ scope.row.num }}</template>
                </el-table-column>
                <el-table-column property="name" label="货物信息" width="120">
                </el-table-column>
                <el-table-column property="address" label="起止站">
                </el-table-column>
                <el-table-column label="单位名称" width="100">
                <template scope="scope">{{ scope.row.work }}</template>
                </el-table-column>
                <el-table-column label="车牌号" width="100">
                <template scope="scope">{{ scope.row.hao }}</template>
                </el-table-column>
                <el-table-column property="msg" label="位置信息" width="100">
                </el-table-column>
                <el-table-column property="status" label="状态">
                </el-table-column>
                <el-table-column label="操作" width="210">
                <template scope="scope">
                      <span class="one">{{ scope.row.one }}</span>
                      <router-link :to="{path:'/motorcade/two'}">
                        <span class="two">{{ scope.row.two }}</span>
                      </router-link>
                      <span class="three">{{ scope.row.three }}</span>
                </template>
                </el-table-column>
                <el-table-column property="" label="查看">
                  <template scope="scope">
                    <router-link :to="{path:'/motorcade/two'}">
                      <el-button type="text">{{ scope.row.look }}</el-button>
                    </router-link>
                  </template>

                </el-table-column>
              </el-table>
            </div>
            <div class="footer clearfix">
              <p>操作功能:</p>
              <div>
                <fu></fu>
              </div>
            </div>
      </div>
  </div>
</template>

<script>
import fu from "@/page/ffu"
import headers from "@/components/header"
export default {
  components:{
    fu,
    headers
  },
data() {
     return {
       tableData: [{
         date: '2016-11-29',
         num:'161129003',
         name: 'Tom',
         address: '河北省 ',
         work:'河北省 ',
         hao:'GA32482',
         msg:'不知道',
         status:'未派车',
         one:'去派车',
         two:" 修改",
         three:' 取消订单',
         look:'详情'
       }, {
         date: '2016-05-02',
         num:'161129003',
         name: 'Tom',
         address: '河北省',
         work:'河北省',
         hao:'GA32482',
         msg:'不知道',
         status:'未派车',
         one:'去派车',
         two:" 修改",
         three:' 取消订单',
         look:'详情'
       }, {
         date: '2016-05-04',
          num:'161129003',
          name: 'Tom',
          address: '河北省',
          work:'河北省',
          hao:'GA32482',
          msg:'不知道',
          status:'未派车',
          one:'去派车',
          two:" 修改",
          three:' 取消订单',
          look:'详情'
       }, {
          date: '2016-05-01',
          num:'161129003',
          name: 'Tom',
          address: '河北省',
          work:'河北省',
          hao:'GA32482',
          msg:'不知道',
          status:'已取消',
          look:'详情'
       }, {
          date: '2016-05-08',
          num:'161129003',
          name: 'Tom',
          address: '河北省',
          work:'河北省',
          hao:'GA32482',
          msg:'不知道',
          status:'待起运',
          one:'去启用',
          two:" 修改",
          three:' 取消订单',
          look:'详情'
       }, {
          date: '2016-05-06',
          num:'161129003',
          name: 'Tom',
          address: '河北省',
          work:'河北省',
          hao:'GA32482',
          msg:'不知道',
          status:'已起运',
          one:'查看轨迹',
          two:' 修改',
          look:'详情'
       }, {
          date: '2016-05-07',
          num:'161129003',
          name: 'Tom',
          address: '河北省',
          work:'河北省',
          hao:'GA32482',
          msg:'不知道',
          status:'已到货',
          one:'查看轨迹',
          two:' 修改',
          three:' 取消订单',
          look:'详情'
       }]
     }
   }
}
</script>

<style lang="scss" scoped>
    .waybill{
      background:#fafbff;
        .clearfix:after{
            content:"";
            display:block;
            clear:both;
        }
        .content{
              width:1200px;
              margin:0 auto;
              padding:36px 0;
                .font{
                  font-size:20px;
                  font-weight:bolder;
                  float:left;
                  color:#869cce;
                    span{
                        font-size:14px;
                        color:#c0c7d8;
                        font-weight:normal;
                    }
                }
                div{
                  float:right;
                }
        }
        .form{
            width:1200px;
            margin:0 auto;
            background:white;
            .input{
                padding:40px 0px 30px 20px;
                border-bottom:2px solid #E6E9F2;
                .el-form-item__label{
                    color:#869cce;
                    font-size:16px;
                }
                input{
                    margin-right:10px;
                }
            }
            .clearfix:after{
              content:"";
              display:block;
              clear:both;
            }
            .table{
              padding:20px;
              .el-table_1_column_11 .cell{
                  cursor:pointer;
                  color:#4A7FE7;
              }
              .el-table_1_column_10 .cell{
                span{
                  cursor:pointer;
                }
                .one{
                  color:#A3D5A0;
                }
                .two{
                  color:#5777B4;
                }
                 .three{
                  color:#B5B5B5;
                }
              }
            }
            .footer{
                margin-top:20px;
                padding-bottom:100px;
                    p{
                      float:left;
                      font-size:14px;
                      color:#becad7;
                      line-height:35px;
                      margin-left:20px
                    }
            }
        }
    }
</style>



